import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable, of } from 'rxjs';
import { map, catchError } from 'rxjs/operators';
import { environment } from '../../../../environments/environment';

interface DollarTokenUser {
  userId: string;
  username: string;
  email: string;
  walletAddress?: string;
  balance: number;
  token: string;
}

@Injectable({
  providedIn: 'root'
})
export class DollarTokenAuthService {
  private readonly STORAGE_KEY = 'dollar_token_user';
  private readonly TOKEN_KEY = 'dollar_token_auth_token';

  constructor(
    private router: Router,
    private http: HttpClient
  ) {}

  /**
   * Î°úÍ∑∏Ïù∏
   */
  login(username: string, password: string): Observable<boolean> {
    console.log('[DollarTokenAuth] Î°úÍ∑∏Ïù∏ ÏãúÎèÑ:', username);
    console.log('[DollarTokenAuth] production:', environment.production);
    
    // Í∞úÎ∞ú ÌôòÍ≤ΩÏóêÏÑúÎäî Î™©ÏóÖ Î°úÍ∑∏Ïù∏ (Ìï≠ÏÉÅ Î™©ÏóÖ ÏÇ¨Ïö©)
    console.log('[DollarTokenAuth] Î™©ÏóÖ Î°úÍ∑∏Ïù∏ ÏÇ¨Ïö©');
    return this.mockLogin(username, password);
  }

  /**
   * Î™©ÏóÖ Î°úÍ∑∏Ïù∏ (Í∞úÎ∞úÏö©)
   */
  private mockLogin(username: string, password: string): Observable<boolean> {
    console.log('[DollarTokenAuth] mockLogin Ìò∏Ï∂ú:', username, password);
    
    const mockUsers: { [key: string]: DollarTokenUser } = {
      'admin': {
        userId: 'dt_admin',
        username: 'admin',
        email: 'admin@dollartoken.com',
        walletAddress: '0x742d35Cc6634C0532925a3b844Bc9e7595f0bEb',
        balance: 10000,
        token: 'mock_token_admin'
      },
      'user1': {
        userId: 'dt_user1',
        username: 'user1',
        email: 'user1@dollartoken.com',
        walletAddress: '0x123456789abcdef',
        balance: 5000,
        token: 'mock_token_user1'
      }
    };

    console.log('[DollarTokenAuth] mockUsers:', Object.keys(mockUsers));
    console.log('[DollarTokenAuth] username exists?', !!mockUsers[username]);
    console.log('[DollarTokenAuth] password match?', password === '1017');
    
    if (mockUsers[username] && password === '1017') {
      console.log('[DollarTokenAuth] Î°úÍ∑∏Ïù∏ ÏÑ±Í≥µ:', mockUsers[username]);
      const user = mockUsers[username];
      this.setUser(user);
      this.setToken(user.token);
      console.log('[DollarTokenAuth] returning of(true)');
      return of(true);
    }

    console.log('[DollarTokenAuth] Î°úÍ∑∏Ïù∏ Ïã§Ìå® - ÏûòÎ™ªÎêú Ïù∏Ï¶ù Ï†ïÎ≥¥');
    console.log('[DollarTokenAuth] username:', username, 'password:', password);
    return of(false);
  }

  /**
   * Î°úÍ∑∏ÏïÑÏõÉ
   */
  logout(): void {
    console.log('[DollarTokenAuth] Î°úÍ∑∏ÏïÑÏõÉ');
    
    try {
      localStorage.removeItem(this.STORAGE_KEY);
      localStorage.removeItem(this.TOKEN_KEY);
    } catch (error) {
      console.error('[DollarTokenAuth] Î°úÍ∑∏ÏïÑÏõÉ Ï§ë Ïò§Î•ò:', error);
    } finally {
      // Ïò§Î•ò Î∞úÏÉù Ïó¨Î∂ÄÏôÄ Í¥ÄÍ≥ÑÏóÜÏù¥ Ìï≠ÏÉÅ Îã¨Îü¨ÌÜ†ÌÅ∞ Î°úÍ∑∏Ïù∏ ÌéòÏù¥ÏßÄÎ°ú Ïù¥Îèô
      this.router.navigate(['/dollar-token/login']).catch((err: any) => {
        console.error('[DollarTokenAuth] ÎÑ§ÎπÑÍ≤åÏù¥ÏÖò ÏòàÏô∏ Î∞úÏÉù:', err);
        window.location.hash = '#/dollar-token/login';
      });
    }
  }

  /**
   * Ïù∏Ï¶ù ÏÉÅÌÉú ÌôïÏù∏
   */
  isAuthenticated(): boolean {
    const token = localStorage.getItem(this.TOKEN_KEY);
    return token !== null && token.trim() !== '';
  }

  /**
   * ÌòÑÏû¨ ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Í∞ÄÏ†∏Ïò§Í∏∞
   */
  getCurrentUser(): DollarTokenUser | null {
    const userStr = localStorage.getItem(this.STORAGE_KEY);
    if (userStr) {
      try {
        return JSON.parse(userStr);
      } catch {
        return null;
      }
    }
    return null;
  }

  /**
   * ÏÇ¨Ïö©Ïûê Ï†ïÎ≥¥ Ï†ÄÏû•
   */
  private setUser(user: DollarTokenUser): void {
    localStorage.setItem(this.STORAGE_KEY, JSON.stringify(user));
  }

  /**
   * ÌÜ†ÌÅ∞ Ï†ÄÏû•
   */
  private setToken(token: string): void {
    localStorage.setItem(this.TOKEN_KEY, token);
  }

  /**
   * ÌÜ†ÌÅ∞ Í∞ÄÏ†∏Ïò§Í∏∞
   */
  getToken(): string | null {
    return localStorage.getItem(this.TOKEN_KEY);
  }

  /**
   * HTTP Ìó§ÎçîÏóê ÌÜ†ÌÅ∞ Ï∂îÍ∞Ä
   */
  getAuthHeaders(): HttpHeaders {
    const token = this.getToken();
    return new HttpHeaders({
      'Content-Type': 'application/json',
      'Authorization': token ? `Bearer ${token}` : ''
    });
  }
}
