import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DollarTokenMainComponent } from './components/dollar-token-main/dollar-token-main.component';
import { DollarTokenLoginComponent } from './components/dollar-token-login/dollar-token-login.component';
import { DollarTokenAuthGuard } from './guards/dollar-token-auth.guard';

const routes: Routes = [
  {
    path: 'login',
    component: DollarTokenLoginComponent
  },
  {
    path: '',
    component: DollarTokenMainComponent,
    canActivate: [DollarTokenAuthGuard]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DollarTokenRoutingModule { }
